body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, p {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.directory-component {
  margin: 2rem;
  padding: 1rem
}

.header {
  font-size: calc(10px + 4vmin);
}

.instructions {
  font-size: calc(10px + 1vmin);
}
.details-componenet {
  margin: 2rem;
  padding: 1rem
}

.card {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.title {
  padding-bottom: 3rem;
}

.spacer {
  padding-top: 2rem;
}

.personal-details-section {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.question-section {
  padding-top: 2rem;
  padding-bottom: 2rem;
  font-style: italic
}
.steps-component {
  margin: 2rem;
  padding: 1rem
}

.cards {
  margin: 1.5rem;
  padding: 1rem
}

.tip {
  padding: 0rem 1rem 0rem 1rem;
  margin: 1rem
}

.inputs {
  padding: 1rem 1rem 0rem 1rem;
}

.input-labels {
  padding: 1rem 0rem 0rem 0rem;
}

h1, h2,  h3, p {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

.update-info {
  padding: .5rem 1rem .5rem 1rem;
}

.update-section-header {
  padding-top: 2rem;
  margin-top: 2rem;
}

.update-button {
margin-top: 2rem;
}

.label {
  padding-top: .5rem;
  margin-top: 1rem;
}

.section-content {
  padding: 0rem 1rem 0rem 1rem;
  margin: 0rem 1rem 0rem 1rem;
}
.info {
    padding: .5rem 1rem .5rem 1rem;
}

.section-header {
  padding-top: 4rem;
}

.edit-button {
  margin-top: 2rem;
}
